.rwx-slide-ticker{position:absolute;top:0px;right:0px;left:0px;bottom:0px;height:100%;width:100%;display:flex;justify-content:center;align-items:center;opacity:0;z-index:-1;overflow:hidden;background:rgba(230,92,0,0.7);cursor:pointer}.rwx-slide-ticker-item{font-size:6rem}.rwx-slide-ticker-item.go span{color:#fff;display:inline-block;-webkit-animation:wiggle 0.5s ease-in-out infinite;animation:wiggle 0.5s ease-in-out infinite}@-webkit-keyframes wiggle{33%{transform:rotate(-20deg) skew(10deg, 10deg)}66%{transform:rotate(20deg) skew(-10deg, -10deg)}100%{transform:rotate(0deg) skew(0deg, 0deg)}}@keyframes wiggle{33%{transform:rotate(-20deg) skew(10deg, 10deg)}66%{transform:rotate(20deg) skew(-10deg, -10deg)}100%{transform:rotate(0deg) skew(0deg, 0deg)}}
